export default {
	data() {
		return {
			lists:'',
			tid:'',
			authKey:'',
			loadingType: 0,
			next : -1,
			start: 0,
			size: 15,
			isaa:0
		};
	},
	onLoad(e){
		this.authKey = uni.getStorageSync('authKey');
		this.jid = e.jid;
	},
	onShow() {
		this.start = 0
		this.redu();
	},
	methods:{
		// 可预约订单
		redu(){
			var that = this;
			let querys = "?start=" + this.start + "&size=" + this.size 
			uni.request({
				url: this.http+'appointment/list' + querys,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
				},
				success: res => {
					let data = res.data.data
					console.log(res.data)
					if (res.data.code == 200) {
						// that.lists = res.data.data.list
						let order = data.list
						if (order.length == 0) {
							that.loadingType = 2
							order = []
						}
						else if (order.length < that.size)
							that.loadingType = 2
						else 
							that.loadingType = 0
						
						if (that.start == 0) 
							that.lists = []
					   
			// 			order.forEach(function(item){
			// 				// item.symbol = item.symbol.toUpperCase()
			// 			})
			            that.start = data.next	
						that.lists = that.lists.concat(order)
						if (that.lists.length == 0) 
							that.record = true
						else 
							that.record = false
					}
					
				},
			});
		},
		chose(id,idx){
			this.isaa = idx;
			uni.navigateTo({
				url: '/pages/reservationService/reservationService?id='+id+'&jid='+this.jid
			});
		},
		gobuy(){
			uni.reLaunch({
				url: '../../service/service'
			});
		}
	}
}
